header{
    background-color: #fcecae;
    width: 100%;

    .app-header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex:1;

        .image-container{
            display: flex;
            flex:3;
            padding-top: 10px;
            padding-top: 5px;
        }

        nav{
            display: flex;
            flex:1;
            justify-content: flex-end;
            justify-content: space-evenly;
            font-size: 1.2em;
            font-weight: 600;
            max-width: 400px;
        }

    }
}