::selection {
    background-color: $clr1;
    color: $clr3;
}

* {
    box-sizing: border-box;
}

html { 
   font-family: Bahnschrift, sans-serif; 
   color: $clr2;
   line-height: 1.8;
   line-height: lh(26px);

   @media (max-width: 1300px) {
       font-size: 13px;
   }
}

body {
    margin: 0;
    background-color: $clr3;
}

a {
    text-decoration: none;
    color: $clr5;
}

a:hover{
    color:#b169b1;
}
