.homepage {
    position: relative;
    color: $clr3;
}

.image-homepage {
    filter: brightness(30%);
    position: absolute;
    top: -20px;
    z-index: -1;
}

.homepage-container{
    text-align: center;
    font-size: 2.5em;
    margin: auto;
    width: 50%;
    padding: 10px;
}

.homepage-container.span a{
font-size: 10px;
}

.toy-link{
    color: #7dbb34;
}

.toy-link:hover{
    color: #b169b1;
}